import React, { Suspense } from "react";
import { LogInPage, ProfilePage, RegistrPage } from "pages";
import { Navigate, RouteProps } from "react-router-dom";
import { SpinerOverlay } from "shared/UI/Spiner/Spiner";

const ProfileConfigPage = React.lazy(() => import("pages").then((module) => ({ default: module.ProfileConfigPage })));
const DialogsPage = React.lazy(() => import("pages").then((module) => ({ default: module.DialogsPage })));
const UsersPage = React.lazy(() => import("pages").then((module) => ({ default: module.UsersPage })));
const MessagesPage = React.lazy(() => import("pages").then((module) => ({ default: module.MessagesPage })));


export const privateRoutes: RouteProps[] = [
    { path: '/profile', element: <ProfilePage /> },
    { path: '/profile/:id', element: <ProfilePage /> },
    {
        path: '/profile/configuration',
        element:
            <Suspense fallback={<SpinerOverlay size={1.5} />}>
                <ProfileConfigPage />
            </Suspense>
    },
    {
        path: '/users',
        element:
            <Suspense fallback={<SpinerOverlay size={1.5} />}>
                <UsersPage />
            </Suspense>
    },
    {
        path: '/dialogs',
        element:
            <Suspense fallback={<SpinerOverlay size={1.5} />}>
                <DialogsPage />
            </Suspense>
    },
    {
        path: '/messages/:id',
        element:
            <Suspense fallback={<SpinerOverlay size={1.5} />}>
                <MessagesPage />
            </Suspense>
    },
    { path: '*', element: <Navigate to="/profile" replace /> },
];

export const publicRoutes: RouteProps[] = [
    { path: '/login', element: <LogInPage /> },
    { path: '/registr', element: <RegistrPage /> },
    { path: '*', element: <Navigate to="/login" replace /> },
];